<template>
 <div class="page-nose">
    <div class="nose" :style="{top: positionTop + 'px', left: positionLeft + 'px'}">
      <div class="nose-trigger" @mouseover="moveImage" @click="catched"></div>
    </div>
 </div>
</template>

<script>
export default {
  name: "PageNose",
  data() {
    return {
      positionTop: 20,
      positionLeft: 20,
    }
  },
  methods: {
    catched() {
      alert('Умничка ты словила!!! Попробуешь еще?')
    },
    moveImage() {
      setTimeout(() => {
        this.positionTop = Math.round(0 - 0.5 + Math.random() * (400 - 1))
        this.positionLeft = Math.round(0 - 0.5 + Math.random() * (1000 - 1))
      }, 120)
    }
  }
}
</script>

<style scoped lang="scss">
.page-nose {
  .nose {
    width: 250px;
    height: 250px;
    background-size: cover;
    background: url("~@/assets/nose.jpg") no-repeat left center;
    position: absolute;

    &-trigger {
      width: 40px;
      height: 40px;
      position: absolute;
      top: 50px;
      left: 62px;
      cursor: pointer;
    }
  }
}
</style>